<template>
  <div class="header">
      <router-link :to="{name:'root'}">
      <img src="../assets/cnodejs_light.svg" alt="">
      </router-link>
      <ul>
          <router-link :to="{name:'root'}">
          <li><a href="#">首页</a></li>
          </router-link>
          <li><a href="#">新手入门</a></li>
          <li><a href="#">API</a></li>
          <li><a href="#">关于</a></li>
          <li><a href="#">登陆</a></li>
          <li><a href="#">注册</a></li>
      </ul>
  </div>
</template>

<script>
export default {
  name: "header"
};
</script>

<style scoped>

.header {
    background-color:#5a5555 ;
    height: 50px;
}

ul {
    float: right;
    list-style: none;
}

li {
    display: inline-block;
    padding: 0px 20px;
}

a {
    text-decoration: none;
    color: #ccc;
    font-size: 14px;
}

img {
    max-width: 120px;
    margin-left: 50px;
    margin-top: 10px;
}

</style>



